      <span
        style="font-family: PingFang;"
      >在使用平台訪問服務前，您應仔細閲讀本協議，並在必要時尋求獨立的法律意見。如果您不同意或不理解本協議，請立即註銷您的賬户並停止使用服務。通過創建賬户、獲取您的賬户識別號和創建密碼、登錄您的賬户或使用服務，您將被視為已理解、完全同意本協議並受本協議（包括不時對本協議進行的任何修改）約束。</span>
